input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  font-weight: 400;
  color:var(--heading-color);
  border-radius: 0;
  background-color: transparent;
  padding: 15px 0 ;
  border: none;
  transition: all 0.4s;
}
textarea {
  height: 145px;
  resize: none;
  @include max(767px) {
    height: 100px;
  }
}

input::-webkit-input-placeholder, 
textarea::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
    color:    var(--heading-color);
    opacity: 1;
}
input:-moz-placeholder, 
textarea:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
   color:    var(--heading-color);
   opacity:  1;
}
input::-moz-placeholder, 
textarea::-moz-placeholder{ /* Mozilla Firefox 19+ */
   color:    var(--heading-color);
   opacity:  1;
}
input:-ms-input-placeholder, 
textarea:-ms-input-placeholder{ /* Internet Explorer 10-11 */
   color:    var(--heading-color);
   opacity: 1;
}
input::-ms-input-placeholder, 
textarea::-ms-input-placeholder{ /* Microsoft Edge */
   color:    var(--heading-color);
   opacity: 1;
}

input::placeholder, 
textarea::placeholder{ /* Most modern browsers support this now. */
   color:    var(--heading-color);
   opacity: 1;
}


[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
  appearance: none;
}

// footer sebscription
.lonyo-subscription{
   &-field{
      text-align: center;
      position: relative;
      max-width: 600px;
      margin: 0 auto;
      .sub-btn{
         position: absolute;
         top: 4px;
         right: 5px;
         padding: 18px 28px;
         color: var(--light-color);
         font-weight: 500;
         transition: all 0.4s;
         &:hover{
            color: var(--heading-color);
         }
         @include max (767px){
            position: relative;
            width: 100%;
            margin-top: 24px;
            @include max (767px){
               margin-top: 15px;
               justify-content: center;
            }
         }
       }
      input{
         padding: 14px 41% 14px 20px;
         border-radius: 3px;
         height: 72px;
         transition: all .4s;
         border: 1px solid var(--heading-color);
         @include max (767px){
            padding: 18px;
         }
      }
   }
   &-field2{
      max-width: 600px;
      text-align: center;
      margin: 0 auto;
      position: relative;
      input{
         transition: all .4s;
         border: 1px solid var(--heading-color);
         border-radius: 3px;
         padding: 14px 20px;
         margin-bottom: 24px;
         @include max (767px){
            padding: 18px;
         }
      }
      .text{
         margin-top: 15px;
         text-align: left;
         font-size: 18px;
         line-height: 25px;
      }
   }
   &-field3{
      input{
         padding: 14px 20px 14px 20px;
         border-radius: 3px;
         transition: all .4s;
         border: 1px solid var(--white-color);
         margin-bottom: 24px;
         @include max (767px){
            padding: 18px;
         }
      }
   }
   
}



