// add acount section

.lonyo-account{
   &-section{
      padding: 230px 0 150px;
      position: relative;
      @include max (991px){
         padding: 190px 0 150px;
      }
      @include max (767px){
         padding: 150px 0 100px;
      }
      &::before{
         top: 90px;
         left: 80px;
         width: 90px;
         height: 90px;
         content: "";
         position: absolute;
         border-radius: 100px;
         background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
         @include max (1800px){
            display: none;
         }
      }
      &::after{
         top: 220px;
         right: 80px;
         width: 200px;
         height: 200px;
         content: "";
         position: absolute;
         border-radius: 100px;
         background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
         @include max (1800px){
            display: none;
         }
      }
      .center{
         max-width: 760px;
      }
   }
   &-title{
      max-width: 400px;
      margin: 0 auto;
      text-align: center;
      padding-bottom: 60px;
      h1{
         padding-bottom: 20px;
      }
   }
   &-box{
      padding: 40px;
      border-radius: 10px;
      border: 1px solid #142D6F1A;
      background-color: var(--white-bg);
      max-width: 600px;
      text-align: center;
      margin: 0 auto;
      @include max (450px){
         max-width: 350px;;
      }
   }
}
.login-with-google{
   background-color: var(--light-color);
   padding: 15px 0;
   border-radius: 3px;
   margin-bottom: 30px;
   a{
      display: flex;
      align-items: center;
      gap: 10px;
      justify-content: center;
   }
}
.or{
   position: relative;
   z-index: 0;
   padding-bottom: 30px;
   p{
      font-weight: 500;
   }
   &::before{
      position: absolute;
      content: "";
      top: 15px;
      left: 0;
      height: 1px;
      width: 100%;
      z-index: -1;
      border-bottom: 2px dashed var(--light-color);
   }
}
