.lonyo-brand{
    &-section{
        padding: 100px 0;
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            padding: 60px 0;
        }
        .title{
            text-align: center;
            padding-bottom: 60px;
            margin: 0;
            @include max (991px){
                padding-bottom: 30px;
            }
        }
        @include max (991px){
            padding-top: 60px;
        }
        @include max (767px){
            padding-top: 60px;
        }
        &.section2{
            padding: 0 0 130px;
            @include max (991px){
                padding: 0 0 80px;
            }
            @include max (767px){
                padding: 0 0 60px;
            }
        }
    }
}
.lonyo-brand-slider-item{
    padding: 0 20px;
    &.ml-10{
        img{
            margin-left: 30px;
        }
    }
}