
.lonyo-default-btn,
.lonyo-subcribe-btn,
#lonyo-submit-btn {
  font-size: $button_font_size;
  line-height: $buton_line_height;
  padding: $button_padding;
  font-weight: $button_font_weight;
  font-family: $button_font_family;
  border-radius: $buton_border_radius;
  width: fit-content;
  position: relative;
  z-index: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-flex;
  text-align: center;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  overflow: hidden;
  color: var(--body-color);
  background-color: var(--heading-bg);
  overflow: hidden;
  transform-style: preserve-3d;
  border: 1px solid var(--heading-color);
  @include max (767px){
    padding: 18px 30px;
  }
  &::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity 0.5s, border 0.5s;
  }
  &::after{
    content: "";
    position: absolute;
    top: 1%;
    left: 0%;
    width: 300px;
    height: 300px;
    background-color: #fff;
    border-color: transparent;
    border-radius: 50%;
    transform: translate(-10px, -70px) scale(0.1);
    opacity: 0;
    z-index: -1;
    transition: transform 0.5s, opacity 0.5s, background-color 0.5s;
  }
  &:hover::after {
    opacity: 1;
    transform: scaleX(1.5) scaleY(1.5);
  }
  &.lonyo-header-btn{
    padding: 12px 24px;
    color: var(--light-color);
    @include max (767px){
      display: none;
    }
    &.btn2{
      color: var(--heading-color);
      background-color: var(--light-color2);
    }
    &:hover{
      color: var(--heading-color);
      &::after {
        opacity: 1;
        transform: scaleX(1.5) scaleY(1.5);
      }
    }
    &::before{
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: opacity 0.5s, border 0.5s;
    }
    &::after{
      content: "";
      position: absolute;
      top: 1%;
      left: 0%;
      width: 200px;
      height: 300px;
      background-color: #fff;
      border-color: transparent;
      border-radius: 50%;
      transform: translate(-10px, -70px) scale(0.1);
      opacity: 0;
      z-index: -1;
      transition: transform 0.5s, opacity 0.5s, background-color 0.5s;
    }
  }
  &.pricing-btn2{
    color: var(--heading-color);
    border: 1px solid var(--heading-color);
    background-color: var(--white-color);
    &:hover{
      color: var(--light-color);
      border: 1px solid var(--heading-color);
    }
    &::after{
      width: 350px;
      height: 350px;
      @include max (1199px){
        width: 400px;
        height: 400px;
      }
      @include max (991px){
        width: 600px;
        height: 600px;
      }
      @include max (767px){
        width: 430px;
        height: 430px;
      }
      @include max (450px){
        width: 330px;
        height: 330px;
      }
      background-color: var(--heading-bg);
    }
  }
  &.pricing-btn{
    color: var(--heading-color);
    border: 1px solid var(--body-color);
    background-color: var(--body-color);
    &:hover{
      color: var(--light-color);
      border: 1px solid var(--heading-color);
    }
    &::after{
      width: 350px;
      height: 350px;
      @include max (1199px){
        width: 400px;
        height: 400px;
      }
      @include max (991px){
        width: 600px;
        height: 600px;
      }
      @include max (767px){
        width: 430px;
        height: 430px;
      }
      @include max (450px){
        width: 330px;
        height: 330px;
      }
      background-color: var(--heading-bg);
    }
  }
  &.t-btn{
    color: var(--heading-color);
    border: 1xp solid var(--body-color);
    background-color: var(--body-color);
    &:hover{
      color: var(--light-color);
    }
    
    &::after{
      width: 300px;
      height: 300px;
      background-color: var(--heading-bg);
    }
  }
  &.hero-btn{
    padding: 18px 25px;
    color: var(--white-color);
    &:hover{
      color: var(--heading-color);
    }
  }
  &.subscrib-btn{
    color: var(--light-color);
   
    &::after{
      @include max (1399px){
        width: 400px;
        height: 400px;
      }
      @include max (1199px){
        width: 450px;
        height: 450px;
      }
      @include max (991px){
        width: 350px;
        height: 350px;
      }
      @include max (767px){
        width: 500px;
        height: 500px;
      }
      @include max (450px){
        width: 350px;
        height: 350px;
      }
    }
    &:hover::after {
      opacity: 1;
      transform: scaleX(1.5) scaleY(1.5);
    }
    &:hover{
      color: var(--heading-color);
    }
  }
  &.subscrib-btn2{
    color: var(--heading-color)!important;
    background-color: var(--body-color)!important;
    &:hover{
      color: var(--heading-color)!important;
    }
    &::after{
      @include max (1399px){
        width: 400px;
        height: 400px;
      }
      @include max (1199px){
        width: 450px;
        height: 450px;
      }
      @include max (991px){
        width: 350px;
        height: 350px;
      }
      @include max (767px){
        width: 500px;
        height: 500px;
      }
      @include max (450px){
        width: 350px;
        height: 350px;
      }
    }

  }
  &.video-btn{
    color: var(--light-color);
    border: 2px solid var(--light-color);
    padding: 16px 22px;
    &:hover{
      color: var(--heading-color);
    }
    &::after{
      width: 230px;
      height: 300px;
   
    }
  }
  &.faq-btn2{
    text-align: center;
    color: var(--light-color);
    &:hover{
      color: var(--heading-color);
    }
  }
  &.cta-btn{
    color: var(--heading-color);
    border: 1px solid var(--body-color);
    background-color: var(--body-color)!important;
    &:hover{
      border: 1px solid var(--heading-color);
    }
  }
  &.sub-btn{
    &::after{
      width: 250px;
      @include max (767px){
        width: 500px;
        height: 500px;
      }
      @include max (450px){
        width: 400px;
        height: 350px;
      }
    }
  }
  &.blog-title-btn{
    color: var(--light-color);
    &:hover{
      color: var(--heading-color);
    }
  }
  &.blog-btn{
    color: var(--heading-color);
    background-color: var(--white-bg);
    border: 1px solid var(--heading-color);
    &:hover{
      color: var(--light-color);
    }
    &::after{
      width: 300px;
      height: 300px;
      background-color: var(--heading-color);
    }
  }
  &.extra-btn{
    color: var(--light-color);
    &:hover{
      color: var(--heading-color);
    }
    &::after{
      width: 650px;
      height: 650px;
      background-color: var(--white-color);
      @include max (767px){
        width: 450px;
        height: 450px;
      }
      @include max (450px){
        width: 300px;
        height: 300px;
      }
    }
  }
  &.extra-btn2{
    color: var(--light-color);
    &:hover{
      color: var(--heading-color);
    }
    &::after{
      width: 800px;
      background-color: var(--white-color);
      @include max (1199px){
        width: 600px;
      }
      @include max (767px){
        width: 450px;
      }
      @include max (450px){
        width: 300px;
      }
    }
  }
  &.team-btn2{
    color: var(--light-color);
    &:hover{
      color: var(--heading-color);
    }
    &::after{
      width: 400px;
      height: 400px;
      
    }
  }
  &.career-btn{
    color: var(--light-color);
    &:hover{
      color: var(--heading-color);
    }
    &::after{
      width: 200px;
    }
  }
  &.d-block {
    width: 100%;
  }
  &.pill {
    border-radius: 50px;
  }
 
  &:focus {
    box-shadow: none;
  }
}

.lonyo-title-btn {
  @include max (991px){
    width: 100%;
    text-align: center;
    margin-top: 30px;
  }
}
.pt-40{
  padding-top: 40px;
  @include max (767px){
    padding-top: 30px;
  }
}

// add animation

.title-btn svg {
  transition: all 0.3s ease-out 0s;
  margin-left: 5px;
}
.title-btn:hover svg path:nth-of-type(1), .title-btn:focus-visible svg path:nth-of-type(1) {
  transform: translateX(calc(-1* var(--arrow-hover-move-x)));
  opacity: 0;
  transition-delay: 0s, 0s;
}
.title-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.title-btn svg path {
  transition: transform 0.38s cubic-bezier(0.37, 0.08, 0.02, 0.93), opacity 0.18s ease-out;
}
.title-btn:hover svg path:nth-of-type(2), .title-btn:focus-visible svg path:nth-of-type(2) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.title-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}
.title-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.title-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}



.title-btn{
  user-select: none;
  -moz-user-select: none;
  background: var(--white-bg) none repeat scroll 0 0;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  text-align: center;
  touch-action: manipulation;
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
  vertical-align: middle;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -o-border-radius: 0;
  -ms-border-radius: 0;
  border-radius: 0;
  white-space: nowrap;
  overflow: hidden;
  border-radius: 50px;
  --arrow-hover-move-x: -110%;
  gap: 10px;
  z-index: 1;
  position: relative;
  border: none;
}