.tag-share-social{
    display: flex;
    gap: 24px;
    align-items: center;
    @include max (767px){
        padding-top: 24px;
    }
    ul{
        display: flex;
        gap: 24px;
        flex-wrap: wrap;
        li{
            display: inline-block;
            a{
                width: 35px;
                height: 35px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 100px;
                transition: all 0.4s;
                border: 1px solid var(--heading-color);
                &:hover{
                    background-color: var(--heading-bg);
                    path{
                        fill: var(--white-bg);
                    }
                }
            }
        }
    }
    &.social2{
        ul{
            li{
                a{
                    width: 45px;
                    height: 45px;
                }
            }
        }
    }
}
.lonyo-social{
    &-wrap{
        ul{
            li{
                display: inline-block;
                margin-right: 15px;
                transition: all 0.4s;
                &:last-child {
                    margin-right: 0;
                }
                a{
                    width: 35px;
                    height: 35px;
                    display: flex;
                    align-items: center;
                    border-radius: 100px;
                    justify-content: center;
                    transition: all 0.4s;
                    border: 1px solid var(--heading-color);
                    &:hover{
                        border: 1px solid transparent;
                        background-color: var(--heading-bg);
                        path{
                            fill: var(--white-bg);
                        }
                    }
                }
            }
        }
    }
    &-wrap2{
        ul{
            li{
                display: inline-block;
                margin-right: 15px;
                transition: all 0.4s;
                &:last-child {
                    margin-right: 0;
                }
                a{
                    width: 35px;
                    height: 35px;
                    display: flex;
                    align-items: center;
                    border-radius: 100px;
                    justify-content: center;
                    transition: all 0.4s;
                    border: 1px solid var(--white-color);
                    &:hover{
                        border: 1px solid transparent;
                        background-color: var(--white-bg);
                        path{
                            fill: var(--heading-bg);
                        }
                    }
                }
            }
        }
    }
}