.lonyo-team{
    &-wrap{
        border-radius: 10px;
        transition: all 0.4s;
        margin-bottom: 24px;
        &:hover{
            .lonyo-team{
                &-thumb img{
                    transform: scale(1.1);
                }
                &-content a h6::before{
                    width: 40%;
                }
                &-content2 a h6::before{
                    width: 40%;
                }
            }
        }

    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        img{
            width: 100%;
            height: 100%;
            transition: all 0.4s;
            object-fit: cover;
            border-radius: 10px;
        }
    }
    &-content{
        padding-top: 24px;
        text-align: center;
        h6{
            font-weight: 500;
            color: var(--light-color);
            position: relative;
            &::before {
                content: " ";
                left: 90px;
                bottom: -3px;
                width: 0;
                height: 1px;
                position: absolute;
                transition: all .4s;
                background: var(--white-color);
                @include max (767px){
                    display: none;
                }
            }
        }
        p{
            margin-bottom: 0;
            margin-top: 10px;
            font-size: 18px;
            line-height: 25px;
            color: var(--light-color);
        }
    } 
    &-content2{
        padding-top: 24px;
        text-align: center;
        h6{
            font-weight: 500;
            position: relative;
            &::before {
                content: " ";
                left: 90px;
                bottom: -3px;
                width: 0;
                height: 1px;
                position: absolute;
                transition: all .4s;
                background: var(--heading-color);
                @include max (767px){
                   display: none;
                }
            }
        }
        p{
            margin-bottom: 0;
            margin-top: 5px;
            font-size: 18px;
            line-height: 25px;
        }
    }
    // add single
    &-d-thumb{
        @include max (991px){
            text-align: center;
            padding-bottom: 40px;
        }
    }
    &-d-content-wrap{
        display: flex;
        align-items: center;
        gap: 24px;
    }
    &-d-content{
        padding-top: 100px;
        @include max (991px){
            padding-top: 60px;
        }
        @include max (767px){
            padding-top: 40px;
        }
        h4{
            padding-bottom: 24px;
        }
        ul{
            li{
                font-weight: 400;
                padding-bottom: 15px;
                padding-left: 20px;
                position: relative;
                &:last-child{
                    padding-bottom: 0;
                }
                span{
                    font-weight: 500;
                }
                &::before{
                    width: 8px;
                    height: 8px;
                    top: 8px;
                    left: 0;
                    content: "";
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
            }
        }
    }
}
.team-btn{
    text-align: center;
    @include max (767px){
        margin-top: 0!important;
    }
}