.lonyo-about-us{
    &-wrap{
        display: flex;
        gap: 24px;
        align-items: center;
        @include max (1199px){
            justify-content: center;
        }
        @include max (767px){
            display: block;
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        transition: all 0.4s;
        margin-bottom: 24px;
        border-radius: 10px;
        @include max (1199px){
            display: flex;
            justify-content: center;
        }
    }
    &-card{
        position: absolute;
        bottom: 0;
        left: 0;
        transition: all 0.4s;
        @include max (767px){
            bottom: -100px;
            left: 50%;
            transform: translate(-50%, -50%);
        }
        @include max (490px){
            bottom: -50px;
        }
        
    }
    &-card2{
        position: absolute;
        bottom: 20px;
        left: 20px;
        transition: all 0.4s;
        @include max (1399px){
            left: 50%;
            bottom: -60px;
            transform: translate(-50%, -50%);
        }
    }
    &-thumb2{
        @include max (991px){
            text-align: center;
            padding-bottom: 40px;
        }
    }
    &-feature-wrap{
        display: flex;
        gap: 24px;
        padding: 30px;
        border-radius: 10px;
        margin-left: 30px;
        background-color: var(--light-color);
        margin-bottom: 24px;
        position: sticky;
        @include max (991px){
            margin-left: 0;
            &:first-child {
                margin-top: 40px;
            }
        }
    }
    &-feature-icon{
        flex-shrink: 0;
    }
    &-feature-content{
        h4{
            padding-bottom: 15px;
        }
        p{
            font-size: 18px;
            line-height: 25px;
        }
    }
}
.max-w616{
    max-width: 616px;
}
@keyframes updown {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-30px);
	}
	100% {
		transform: translatey(0px);
	}
}

