.lonyo-hero{
    &-shape {
        position: absolute;
        top: 80px;
        left: 20px;
        z-index: -1;
        transform: translatey(0);
        -webkit-animation: updown 3s ease-in-out infinite;
        animation: updown 3s ease-in-out infinite;
        transform: translate(-50px, -50px);
        @include max (991px){
            left: 100px;
        }
        @include max (767px){
            display: none;
        }
    }
    &-shape2 {
        
        img{
            position: absolute;
            top: 0;
            left: 0;
        }
        &::before{
            position: absolute;
            content: "";
            top: 40%;
            right: 100px;
            transform: translatey(0);
            -webkit-animation: updown 3s ease-in-out infinite;
            animation: updown 3s ease-in-out infinite;
            width: 150px;
            height: 150px;
            border-radius: 100px;
            background: linear-gradient(48.01deg, #a3bdff -82.43%, #142d6f 89.75%);
            @include max (1800px){
                display: none;
            }
        }
        @include max (991px){
            left: 100px;
        }
        @include max (767px){
            display: none;
        }
    }
    &-shape3 {
            position: absolute;
            top: 0;
            left: 0;
            transform: translatey(0);
            -webkit-animation: updown 3s ease-in-out infinite;
            animation: updown 3s ease-in-out infinite;
    }
}

// end hero
.lonyo-content{
    &-shape1{
        @include max (767px){
            margin-top: -6px;
        }
    }
    &-shape2{
        width: 120px;
        height: 120px;
        bottom: 170px;
        right: 60px;
        position: absolute;
        border-radius: 100px;
        transform: translatey(0);
        -webkit-animation: updown 3s ease-in-out infinite;
        animation: updown 3s ease-in-out infinite;
        background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
        @include max (1800px){
            display: none;
        }
    }
    &-shape3{
        z-index: 0;
        position: relative;
    }
}
.lonyo-tab-shape{
    position: absolute;
    top: 0;
    right: 0;
    @include max (1800px){
        display: none;
    }
}
.lonyo-cta-shape1{
    position: absolute;
    top: 0;
    left: 0;
    @include max (1800px){
        display: none;
    }
}
.pricing-shape{
    width: 150px;
    height: 150px;
    bottom: 24%;
    left: 50px;
    transform: translatey(0);
    -webkit-animation: updown 3s ease-in-out infinite;
    animation: updown 3s ease-in-out infinite;
    transform: rotate(180deg);
    position: absolute;
    border-radius: 100px;
    background: linear-gradient(47.56deg, #a3bdff -40.67%, #f6f4ef 78.6%);
    @include max (1800px){
        display: none;
    }
}
.team-section{
    position: relative;
    background-color: var(--heading-bg);
    h2{
        color: var(--white-color);
    }
    .shape{
        position: absolute;
        top: 0;
        right: 0;
        @include max (1899px){
            display: none;
        }
    }
   
}
.lonyo-feature-shape{
    width: 130px;
    height: 130px;
    bottom: 45px;
    left: 6%;
    position: absolute;
    transform: translatey(0);
    -webkit-animation: updown 3s ease-in-out infinite;
    animation: updown 3s ease-in-out infinite;
    border-radius: 100px;
    background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
    @include max (1800px){
        display: none;
    }
    &.shape2{
        width: 150px;
        height: 150px;
        bottom: 40%;
        left: 4%;
        position: absolute;
        border-radius: 100px;
        transform: rotate(145deg);
        background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
        @include max (1800px){
            display: none;
        
        }
    }
    &.shape3{
        width: 150px;
        height: 150px;
        top: 15%;
        left: 4%;
        position: absolute;
        border-radius: 100px;
        transform: rotate(145deg);
        background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
        @include max (1800px){
            display: none;
        
        }
    }
}
.lonyo-dasyboard-shape,
.lonyo-cta-shape2{
    @include max (767px){
        margin-top: -6px;
    }
}
.lonyo-brand-shape {
    position: relative;
    margin-top: -40px;
    transform: translatey(0);
    -webkit-animation: updown 3s ease-in-out infinite;
    animation: updown 3s ease-in-out infinite;
    @include max (1699px){
        display: none;
    }
}