.lonyo-service{
    &-wrap{
        border-radius: 10px;
        padding: 30px;
        margin-bottom: 24px;
        transition: all 0.4s;
        @include max (767px){
            padding: 30px 20px;
        }
        &:hover{
            box-shadow: -4px 10px 20px 0px #142D6F1A;
        }
    }
    &-title{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 24px;
    }
    &-data{
        p{
            font-size: 18px;
            line-height: 1.39em;
        }
    }
    // end one
    &-wrap2{
        padding: 30px 30px 200px 30px;
        border-radius: 10px;
        position: relative;
        overflow: hidden;
        margin-bottom: 24px;
        @include max (1399px){
            padding: 30px 40px 218px 30px;
            margin-bottom: 40px;
        }
        @include max (767px){
            padding: 30px;
        }
    }
    &-icon{
        padding-bottom: 24px;
    }
    &-content{
        h4{
            padding-bottom: 15px;
        }
        p{
            font-size: 18px;
            line-height: 25px;
            max-width: 345px;
        }
    }
    &-wrap3{
        padding: 30px 40px 0 30px;
        border-radius: 10px;
        margin-bottom: 24px;
    }
    &-thumb3{
        &.thumb4{
            margin-bottom: 24px;
        }
        @include max (1199px){
            text-align: center;
        }
    }
    &-icon{
        padding-bottom: 24px;
    }
    &-content2{
        h4{
            padding-bottom: 15px;
        }
        p{
            font-size: 18px;
            line-height: 25px;
        }
    }
    &-vertical-slider{
        position: absolute;
        top: 0;
        left: 63%;
        @include max (1399px){
            left: 59%;
        }
        @include max (767px){
            display: none;
        }
    }
    &-vertical-item{
        padding: 10px 0;
    }
    &-wrap4{
        padding: 30px 30px 0 30px;
        border-radius: 10px;
        margin-bottom: 24px;
        @include max (1399px){
            padding: 30px 40px 0 30px;
            margin-bottom: 40px;
        }
        @include max (767px){
        }
    }
    &-icon{
        padding-bottom: 24px;
    }
    &-content{
        h4{
            padding-bottom: 15px;
        }
        p{
            font-size: 18px;
            line-height: 25px;
            max-width: 345px;
        }
    }
    &-thumb{
        padding-top: 24px;
        position: relative;
        z-index: 0;
        @include max (991px){
            text-align: center;
        }
        &::before{
            position: absolute;
            content: "";
            bottom: 0;
            left: 20px;
            z-index: -1;
            width: 350px;
            height: 170px;
            border-radius: 10px 10px 0 0;
            background-color: var(--light-color3);
            @include max (1199px){
                width: 300px;
                height: 135px;
            }
            @include max (991px){
                left: 150px;
                height: 170px;
                width: 355px;
            }
            @include max (767px){
                display: none;
            }
        }
        &::after{
            position: absolute;
            content: "";
            bottom: 0;
            left: 40px;
            z-index: -2;
            width: 350px;
            height: 150px;
            border-radius: 10px 10px 0 0;
            background-color: var(--light-color4);
            @include max (1199px){
                width: 290px;
                height: 112px;
            }
            @include max (991px){
                left: 230px;
                height: 150px;
            }
            @include max (767px){
                display: none;
            }
        }
    }
    &-thumb2{
        position: absolute;
        top: 80px;
        right: 30px;
        @include max (1399px){
            top: 250px;
            left: 25%;
            transform: translate(-50%, -50%);
        }
 
    
        @include max (767px){
            left: 21%;
        }
       
    }
    &-wrap5{
        padding: 30px 30px 230px 30px;
        border-radius: 10px;
        position: relative;
        overflow: hidden;
        margin-bottom: 24px;
        @include max (1399px){
            padding: 30px 40px 230px 30px;
        }
        @include max (1199px){
            padding: 30px 40px 208px 30px;
        }
        @include max (991px){
            padding: 30px 40px 300px 30px;
        }
        @include max (767px){
            padding: 30px 40px 170px 30px;
        }
    }
    &-icon{
        padding-bottom: 24px;
    }
    &-content{
        h4{
            padding-bottom: 15px;
        }
        p{
            font-size: 18px;
            line-height: 25px;
            max-width: 345px;
        }
    }
}
.lonyo-content{
    &-thumb{
        text-align: center;
        position: relative;
        @include max (991px){
            padding-bottom: 40px;
        }
        &::before{
            position: absolute;
            content: "";
            top: 50%;
            left: 50%;
            z-index: -1;
            width: 550px;
            height: 550px;
            border-radius: 100%;
            transform: translate(-50%, -50%);
            background: linear-gradient(47.56deg, #A3BDFF -40.67%, #FFFFFF 78.6%);
            @include max (1800px){
                display: none;
            }
        }
    }
    &-thumb2{
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
}

// add tabs

.tabs{
    display: flex;
    align-items: center;
    gap: 50px;
    transition: all 0.4s;
    flex-wrap: wrap;
    @include max (991px){
        gap: 30px;
    }
    .active-tab{
        &::before{
            content: "";
            bottom: 0;
            width: 100%;
            height: 3px;
            position: absolute;
            border-radius: 3px;
            transition: all 0.4s;
            margin-bottom: -10px;
            border-bottom: 3px solid var(--light-color2);
        }
    }
    li{
        display: inline-flex;
        gap: 10px;
        align-items: center;
        position: relative;
        cursor: pointer;
        &::before{
            content: "";
            bottom: 0;
            border-radius: 3px;
            width: 100%;
            height: 3px;
            position: absolute;
            margin-bottom: -10px;
            border-bottom: 3px solid var(--light-color);
        }
    }
    &.tab2{
        justify-content: center;
    }
}
.border-bottom{
    position: absolute;
    bottom: 224px;
    max-width: 1180px;
    left: 16%;
    border-bottom: 3px solid #F6F4EF1A!important;
    @include max (1800px){
        display: none;
    }
}

.tabs-content {
    margin-top: 30px;
    transition: all 0.4s;
	li {
		display: none;
	}
    &.content2{
        margin-top: 70px;
        @include max (767px){
            margin-top: 50px;
        }
        li{
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}
.lonyo-process{
    &-wrap{
        transition: all 0.4s;
        margin-top: 100px;
        padding-right: 43px;
        @include max (1199px){
            &:last-child{
                margin-top: 50px;
            }
        }
        @include max (991px){
            margin-top: 80px;
            &:last-child{
                margin-top: 30px;
            }
        }
        @include max (767px){
            margin-top: 60px;
        }
        &:hover{
            .lonyo-process-title{
                &::before{
                    display: block;
                }
            }
        }
    }
    &-number{
        padding-bottom: 24px;
    }
    &-title{
        padding-bottom: 30px;
        position: relative;
        &::before{
            content: "";
            width: 310px;
            height: 3px;
            transition: all 0.4s;
            bottom: 19px;
            left: 0;
            position: absolute;
            display: none;
            background-color: var(--light-color2);
        }
        h4{
            color: var(--light-color);
        }
    }
    &-data{
        p{
            margin: 0;
            font-size: 18px;
            font-weight: 400;
            line-height: 25px;
            color: var(--light-color);
        }
    }
}


// add home three tab section
.lonyo-tab{
    &-section{
        position: relative;
        background-color: var(--heading-color);
        &::before{
            top: 50%;
            left: 40px;
            width: 150px;
            height: 150px;
            content: "";
            position: absolute;
            transform: translatey(0);
            -webkit-animation: updown 3s ease-in-out infinite;
            animation: updown 3s ease-in-out infinite;
            border-radius: 100px;
            background: linear-gradient(48.01deg, #A3BDFF -82.43%, #142D6F 89.75%);
            @include max (1800px){
                display: none;
            }
        }
        h2{
            color: var(--light-color);
        }
    }
    &-wrap{
        ul{
            li{
                color: var(--light-color);
                h4{
                    color: var(--light-color);
                }
            }
        }
    }
}

.lonyo-rating{
    &-section{
        padding-top: 100px;
        @include max (991px){
            padding-top: 80px;
        }
        @include max (767px){
            padding-top: 60px;
        }
        &.section2{
            padding: 106px 0 130px;
            @include max (991px){
                padding: 76px 0 80px;
            }
            @include max (767px){
                padding: 32px 0 60px;
            }
        }
    }
    &-wrap{
        text-align: center;
        @include max (1199px){
            padding: 20px;
        }
        .pt-0{
            @include max (1199px){
                padding-top: 0;
            }
        }
        .pt-24{
            @include max (1199px){
                padding-top: 24px;
            }
        }
    }
    &-icon{
        padding-bottom: 24px;
    }
    &-content{
        span{
            font-weight: 500;
        }
    }
}

.lonyo-service-vertical-slider2{
    position: absolute;
    top: 0;
    left: 18px;
    @include max (1399px){
        left: -1px;
    }
    @include max (1199px){
        left: 18px;
    }
    @include max (767px){
        left: 122px;
    }
    @include max (525px){
        left: 100px;
    }
    @include max (450px){
        left: 80px;
    }
    @include max (400px){
        left: 40px;
    }
}

// add integration

.max-w1000{
    max-width: 1000px;
}
.lonyo-integration{
    &-wrap{
        display: flex;
        gap: 24px;
        padding: 30px 20px;
        margin-bottom: 24px;
        border-radius: 10px;
        transition: all 0.4s;
        border: 1px solid #142D6F1A;
        &:hover{
            box-shadow: -4px 10px 20px 0px #142D6F1A;
            border: 1px solid var(--white-color);
        }
    }
    &-icon{
        flex-shrink: 0;
    }
    &-content{
        h4{
            padding-bottom: 12px;
        }
        p{
            font-size: 18px;
            line-height: 25px;
        }
        a{
            font-weight: 500;
            color: var(--heading-color);
            svg{
                margin-top: 5px;
            }
        }
    }
    // single-integration
    &-d-wrap{
        max-width: 1050px;
        margin: 0 auto;
    }
    &-d-title{
        padding-bottom: 80px;
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        ul{
            display: flex;
            align-items: center;
            gap: 30px;
            justify-content: center;
        }
    }
}
.dividead{
    border-top: 1px solid #142D6F1A;
}



.lonyo-service-table{
    &-item {
        display: flex;
        padding: 30px;
        border-radius: 10px;
        margin-bottom: 24px;
        align-items: center;
        background-color: var(--light-color);
        @include max (991px){
            display: block;
        }
    }
    &-title {
        flex: 0 0 30%;
        h4{
            padding-bottom: 12px;
        }
    }
    &-body {
        flex: 0 0 50%;
        padding: 0 0 0 40px;
         @include max (991px){
            padding: 15px 0;
        }
        ul{
            li{
                display: inline-block;
                margin-right: 10px;
                font-size: 18px;
                line-height: 25px;
                &:last-child{
                    margin-right: 0;
                }
                img{
                    padding-right: 5px;
                }
                @include max (767px){
                    display: block;
                    margin-bottom: 10px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
    &-btn {
        flex: 0 0 20%;
        text-align: right;
        @include max (991px){
            text-align: left;
        }
    }
}
