.lonyo-blog{
    &-wrap{
        padding: 30px;
        border-radius: 10px;
        margin-bottom: 24px;
        transition: all 0.4s;
        border: 1px solid #142D6F1A;
        &:hover{
            border: 1px solid var(--white-bg);
            box-shadow: -4px 10px 20px 0px #142D6F1A;
            .lonyo-blog-thumb img{
                transform: scale(1.1);
            }
            .lonyo-blog-content{
                h2{
                    background-size: 100% 1px;
                }
            }
        }
    }
    &-thumb{
        border-radius: 15px;
        margin-bottom: 24px;
        position: relative;
        overflow: hidden;
        img{
            width: 100%;
            height: 200%;
            object-fit: cover;
            transition: all 0.4s;
        }
    }
    &-meta{
        padding-bottom: 24px;
        ul{
            li{
                display: inline-block;
                margin-right: 24px;
                position: relative;
                padding-left: 30px;
                &:last-child{
                    margin-right: 0;
                    @include max (394px){
                    margin-top: 10px;
                    }
                }
                
                a{
                    font-size: 18px;
                    line-height: 25px;
                    color: var(--heading-color);
                    img{
                        position: absolute;
                        top: 4px;
                        left: 0;
                    }
                }
            }
        }
    }
    &-content{
        padding-bottom: 24px;
        a{
            color: var(--heading-color);
        }
        h2{
            padding-top: 0;
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0 100%;
            background-repeat: no-repeat;
            background-size: 0 1px;
            transition: background-size .3s ease-in-out;
            display: inline;
            font-size: 48px;
            line-height: 60px;
            letter-spacing: -0.5px;
            @include max(1199px) {
                font-size: 36px;
                line-height: 42px;
            }
            @include max(767px) {
                font-size: 28px;
                line-height: 36px;
            }
        }
        p{
            margin-top: 15px;
        }
    }
    // strat inner page
    &-sidebar{
        padding-left: 30px;
        @include max (991px){
            margin-top: 40px;
            padding-left: 0;
        }
    }
    &-widgets {
        margin-bottom: 60px;
        &:last-child{
            margin-bottom: 0;
        }
        h4{
            padding-bottom: 24px;
        }
        @include max (991px){
            margin-bottom: 40px;
        }
        &.widgets2{
            display: flex;
            align-items: center;
            gap: 24px;
            margin-bottom: 0;
            h4{
                padding-bottom: 0;
            }
        }
    }
    &-categorie{
       ul{
        li{
            margin-bottom: 15px;
            &:last-child{
                margin-bottom: 0;
            }
            a{
                color: var(--heading-color);
                display: flex;
                align-items: center;
                justify-content: space-between;
            }
        }
       }
    }
    &-recent-post-item{
        display: flex;
        align-items: center;
        gap: 15px;
        margin-bottom: 24px;
        @include max (1199px){
            gap: 24px;
        }
        &:last-child{
            margin-bottom: 0;
        }
        &:hover{
            .lonyo-blog-recent-post-thumb img{
                transform: scale(1.1);
            }
            .lonyo-blog-recent-post-data{
                h4{
                    background-size: 100% 1px;
                }
            }
        }
    }
    &-recent-post-thumb {
        border-radius: 10px;
        overflow: hidden;
        flex-shrink: 0;
        transition: all 0.4s;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
        }
    }
    &-recent-post-data{
        ul {
            li{
                font-size: 18px;
                line-height: 25px;
                color: var(--heading-color);
                margin-bottom: 10px;
                position: relative;
                padding-left: 30px;
                img{
                    position: absolute;
                    top: 0;
                    left: 0;
                }
            }
        }
        h4{
            margin-top: 5px;
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0 100%;
            background-repeat: no-repeat;
            background-size: 0 1px;
            transition: background-size .3s ease-in-out;
            display: inline;
            padding-bottom: 0;
            font-size: 22px;
        }
    }
    &-tags{
        ul{
            display: flex;
            gap: 10px;
            flex-wrap: wrap;
            li{
                display: inline-block;
                a{
                    font-size: 18px;
                    line-height: 25px;
                    padding: 8px 16px;
                    display: block;
                    border-radius: 3px;
                    transition: all .4s;
                    color: var(--heading-color);
                    background-color: #142D6F0D;
                    &:hover{
                        color: var(--light-color);
                        background-color: var(--heading-bg);
                    }
                }
            }
        }
    }
    // single page
    &-d-thumb{
        padding-bottom: 24px;
    }
    &-d-content{
        padding-top: 40px;
        h2{
            padding-bottom: 20px;
        }
        a{
            color: var(--heading-color);
        }
        ul{
            li{
                font-weight: 500;
                position: relative;
                margin-bottom: 15px;
                padding-left: 30px;
                &::before{
                    content: "";
                    position: absolute;
                    top: 10px;
                    left: 0;
                    width: 10px;
                    height: 10px;
                    border-radius: 100px;
                    background-color: var(--heading-color);
                }
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
    }
    &-d-content-wrap{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 30px 0;
        border-top: 1px solid #142D6F1A;
        border-bottom: 1px solid #142D6F1A;
        margin: 40px 0;
        @include max (767px){
            margin: 30px 0;
            display: block;
        }
    }
    &-d-comment-box{
        h4{
            padding-bottom: 30px;
        }
    }
    &-d-comment-wrap1{
        display: flex;
        .reply-btn a{
            font-weight: 500;
            padding-top: 10px;
            color: var(--heading-color);
        }
        &.wrap2{
            padding-bottom: 24px;
            border-bottom: 1px solid #142D6F1A;
            margin-bottom: 24px;
        }
    }
    &-d-comment-wrap{
        display: flex;
        padding-bottom: 24px;
        border-bottom: 1px solid #142D6F1A;
        margin-bottom: 30px;
        padding-top: 24px;
        border-top: 1px solid #142D6F1A;
        margin-top: 30px;
        .reply-btn a{
            font-weight: 500;
            padding-top: 10px;
            color: var(--heading-color);
        }
    }
    &-d-comment-thumb{
        margin-right: 20px;
        flex-shrink: 0;
        img{
            border-radius: 100px;
        }
    }
    &-d-comment-data1{
        span{
            font-size: 16px;
            line-height: 22px;
        }
        p{
            padding-top: 15px;
        }
    }
    &-d-comment-data{
        span{
            font-size: 16px;
            line-height: 22px;
        }
        p{
            color: var(--dark-color1);
            padding-top: 10px;
        }
    }
    &-d-comment-box2{
        h4{
            padding-bottom: 30px;
        }
    }
    &-d-content-comment-box{
        h4{
            margin-bottom: 30px;
        }
    }
}



.mt-10{
    margin-top: 10px;
}
.lonyo-recent-post-item:hover .lonyo-recent-post-data h6 {
    background-size: 100% 1px;
}
.lonyo-search-box {
    position: relative;
    border-radius: 3px;
    padding: 0 45px 0 18px;
    border: 1px solid #142D6F26;
    input{
        font-size: 18px;
        line-height: 25px;
    }
}
button#lonyo-search-btn {
    position: absolute;
    right: 20px;
    top: 20px;
}
blockquote {
    margin-top: 40px;
    position: relative;
    border-radius: 10px;
    padding: 30px;
    background-color: var(--light-color);
    &::before{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 6px;
        height: 100%;
        border-radius: 10px 0px 0px 10px;
        background-color: var(--heading-bg);
    }
}

.pl-101{
    margin-left: 101px;
    @include max (767px){
        margin-left: 0;
    }
}
.deivdead-line{
    padding-top: 100px;
    border-bottom: 1px solid rgba(20, 45, 111, .1019607843);
    margin-bottom: 100px;
    @include max (991px){
        padding-top: 80px;
        margin-bottom: 80px;
    }
    @include max (767px){
        padding-top: 60px;
        margin-bottom: 60px;
    }
}

.lonyo-blog-d-thumb {
    img{
        width: 100%;
    }
}