.breadcrumb{
  &-wrapper{
    z-index: 0;
    text-align: center;
    padding: 200px 0 130px;
    position: relative;
    transition: all 0.4s;
    &::before{
      content: "";
      position: absolute;
      width: 200px;
      height: 200px;
      bottom: 200px;
      right: 50px;
      transform: translatey(0);
      -webkit-animation: updown 2s ease-in-out infinite;
      animation: updown 2s ease-in-out infinite;
      border-radius: 100px;
      background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
      @include max (1800px){
        display: none;
      }
    }
    &::after{
      content: "";
      position: absolute;
      width: 90px;
      height: 90px;
      top: 115px;
      left: 50px;
      transform: translatey(0);
      -webkit-animation: updown 3s ease-in-out infinite;
      animation: updown 3s ease-in-out infinite;
      border-radius: 100px;
      background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
      @include max (1800px){
        display: none;
      }
    }
    @include max(1199px) {
      padding: 180px 0 100px;
    }
    @include max(767px) {
      padding: 120px 0 80px;

    }
  }
  &-content {
    position: relative;
    max-width: 850px;
    text-align: center;
    margin: 0 auto;
    p{
      margin: 0;
    }
  }
  &-title{
    padding-bottom: 20px;
  }
  &-menu{
    ul{
      li{
        font-weight: 500;
        display: inline-block;
        padding: 0 10px;
        a{
          color: var(--heading-color);
        }
      }
    }
    &-wrap{
      padding-top: 50px;
      @include max (1199px){
        padding-top: 30px;
      }
    }
  }
}




.lonyo-errors{
  &-section{
    padding: 330px 0 130px;
    @include max(991px) {
      padding: 250px 0 100px;
    }
    @include max(767px) {
      padding: 170px 0 80px;
    }
  }
  &-content{
    max-width: 650px;
    margin: 0 auto;
    text-align: center;
    img{
      margin-bottom: 40px;
    }
    h2{
      margin-bottom: 15px;
    }
  }
}