.lonyo-p{
    &-wrap{
        &:hover{
            .lonyo-p-thumb img{
                scale: 1.1;
            }
        }
      
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        margin: 24px;
        &.thumb2{
            padding: 0;
        }
        @include max (991px){
            margin-bottom: 24px;
        }
        img{
            border-radius: 10px;
            transition: all 0.4s;
            object-fit: cover;
            &:hover{
                transform: scale(1.1);
            }
        }
    }
    &-data-wrap{
        display: flex;
        align-items: center;
        position: absolute;
        flex-wrap: wrap;
        padding: 20px;
        background-color: var(--white-color);
        bottom: 50px;
        left: 30px;
        width: 90%;
        border-radius: 10px;
        justify-content: space-between;
        @include max (1399px){
            left: 20px;
        }
        @include max (991px){
            bottom: 20px;
            left: 15px;
        }
        @include max (767px){
            bottom: 30px;
        }
        &.wrap2{
            padding: 15px;
            @include max (1399px){
                gap: 0;
                left: 15px;
            }
            @include max (575px){
                display: block;
                bottom: 30px;
            }
        }
    }
    &-content{
        h4{
            padding-bottom: 12px;
        }
        ul{
            li{
                position: relative;
                display: inline-block;
                padding-left: 20px;
                margin-right: 30px;
                margin-bottom: 5px;
                @include max (767px){
                    margin-right: 10px;
                }
                &:last-child{
                    margin-bottom: 0;
                }
                &::before{
                    position: absolute;
                    content: "";
                    width: 10px;
                    height: 10px;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                    top: 8px;
                    left: 0;
                }
                a{
                    color: var(--heading-color);
                }
            }
        }
    }
    &-title-btn{
        @include max (919px){
            padding-top: 10px;
        }
        a{
            font-weight: 500;
            color: var(--accent-color);
            img{
                padding-left: 5px;
            }
        }
    }
    // inner page
    &-d-data-wrap{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 24px;
        justify-content: space-between;
        @include max (767px){
            display: block;
        }
    }
    &-d-thumb{
        margin-bottom: 24px;
        border-radius: 10px;
    }
    &-d-data-text{
        text-align: left;
    }
}
.lonyo-portfolio-menu{
    ul{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 24px;
        flex-wrap: wrap;
        li{
            border: 1px solid var(--heading-color);
            color: var(--heading-color);
            border-radius: 3px;
            padding: 16px 26px;
            cursor: pointer;
            font-weight: 500;
            @include max(767px){
                margin-bottom: 24px;
            }
            &:last-child{
                margin-bottom: 0;
                margin-right: 0;
            }
            &.active{
                background: var(--body-color);
                color: var(--heading-color);
                border: 1px solid var(--body-color);
            }
        }
    }
}


.w100{
    width: 100%!important;
}
