.lonyo-section-padding{
    padding: 130px 0;
    overflow: hidden;
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
    &.sectionn{
        @include max (767px){
            margin-top: -2px;
            z-index: 1;
        }
    }
}
.lonyo-section-padding2{
    padding: 34px 0 106px;
    overflow: hidden;
    @include max (991px){
        padding: 34px 0 76px;
    }
    @include max (767px){
        padding: 14px 0 32px;
    }
}
.lonyo-section-padding3{
    padding: 0 0 130px;
    overflow: hidden;
    @include max (991px){
        padding: 0 0 80px;
    }
    @include max (767px){
        padding: 0 0 60px;
    }
}
.lonyo-section-padding4{
    padding: 0 0 35px;
    overflow: hidden;
    @include max (991px){
        padding: 0 0 20px;
    }
    &.section{
        padding-top: 130px;
        @include max (991px){
           padding-top: 80px;
        }
        @include max (767px){
            padding-top: 60px;
        }
        position: relative;
        &::before{
            width: 150px;
            height: 150px;
            top: 24%;
            left: 80px;
            content: "";
            transform: translatey(0);
            -webkit-animation: updown 3s ease-in-out infinite;
            animation: updown 3s ease-in-out infinite;
            position: absolute;
            border-radius: 100px;
            background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
            @include max (1800px){
                display: none;
            }
        }
    }
}
.lonyo-section-padding5{
    overflow: hidden;
    padding: 35px 0 130px;
    @include max (991px){
        padding: 0 0 80px;
    }
    @include max (767px){
        padding: 0 0 60px;
    }
}
.lonyo-section-padding6{
    overflow: hidden;
    padding: 0 0 106px;
    @include max (991px){
        padding: 0 0 76px;
    }
    @include max (767px){
        padding: 0 0 36px;
    }
}
.lonyo-section-padding7{
    overflow: hidden;
    padding: 130px 0 0;
    @include max (991px){
        padding: 80px 0 0;
    }
    @include max (767px){
        padding: 60px 0 0;
    }
    &.section{
        position: relative;
        &::before{
            width: 150px;
            height: 150px;
            top: 24%;
            left: 80px;
            content: "";
            position: absolute;
            transform: translatey(0);
            -webkit-animation: updown 3s ease-in-out infinite;
            animation: updown 3s ease-in-out infinite;
            border-radius: 100px;
            transform: rotate(145deg);
            background: linear-gradient(47.56deg, #a3bdff -40.67%, #f6f4ef 78.6%);
            @include max (1800px){
                display: none;
            }
        }
    }
    &.section2{
        overflow: hidden;
        position: relative;
        &::before{
            width: 130px;
            height: 130px;
            top: 50%;
            right: 80px;
            content: "";
            position: absolute;
            transform: translatey(0);
            -webkit-animation: updown 3s ease-in-out infinite;
            animation: updown 3s ease-in-out infinite;
            border-radius: 100px;
            background: linear-gradient(47.56deg, #a3bdff -40.67%, #f6f4ef 78.6%);
            @include max (1800px){
                display: none;
            }
        }
    }
}
.lonyo-section-padding9{
    overflow: hidden;
    padding: 130px 0 35px;
    @include max (991px){
        padding: 80px 0 35px;
    }
    @include max (767px){
        padding: 60px 0 35px;
    }
}
.lonyo-section-padding10{
    overflow: hidden;
    padding: 130px 0 106px;
    @include max (991px){
        padding: 80px 0 56px;
    }
    @include max (767px){
        padding: 60px 0 36px;
    }
}


// section title

.lonyo-section-title{
    padding-bottom: 60px;
    @include max (991px){
        padding-bottom: 40px;
        text-align: center;
    }
    @include max (767px){
        padding-bottom: 30px;
    }
    &.pb-40{
        padding-bottom: 40px;
        @include max (767px){
            padding-bottom: 30px;
        }
    }
    .title{
        padding-bottom: 20px;
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-700{
            max-width: 700px;
        }
        &.max-width-750{
            max-width: 750px;
        }
        &.max-width-760{
            max-width: 760px;
        }
    }
    .text2{
        color: var(--light-color2);
    }
    &.max600{
        max-width: 600px;
    }
    .max-w700{
        max-width: 700px;
    }
}
// default content
.lonyo-default-content{
    h2{
        padding-bottom: 20px;
    }
    &.pb-35{
        padding-bottom: 35px;
        @include max(767px){
            padding-bottom: 30px;
        }
        p{
            margin-bottom: 30px;
            &:last-child{
                margin-bottom: 0;
            }
        }
        ul{
            li{
                position: relative;
                padding-left: 20px;
                margin-bottom: 15px;
                span{
                    font-weight: 500;
                }
                &:last-child{
                    margin-bottom: 0;
                }
                &::before{
                    width: 8px;
                    height: 8px;
                    top: 8px;
                    left: 0;
                    content: "";
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
            }
        }
        .text{
            p span{
                font-size: 28px;
                font-weight: 700;
                line-height: 36px;
                letter-spacing: -0.5px;
            }
            &.text2{
                p{
                    margin-bottom: 24px;
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
    &.max-1100{
        max-width: 1070px;
        ul{
            li{
                font-size: 20px;
                line-height: 26px;
                margin-bottom: 15px;
                padding-left: 20px;
                position: relative;
                &::before{
                    width: 8px;
                    height: 8px;
                    top: 8px;
                    left: 0;
                    content: "";
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
                &:last-child{
                    margin-bottom: 0;
                }
                span{
                    font-weight: 500;
                }
            }
        }
        .text3{
            margin-top: 24px;
            max-width: 810px;
        }
    }
    &.border-b{
        padding-bottom: 100px;
        border-bottom: 1px solid #142D6F1A;
        margin-bottom: 100px;
        @include max (991px){
            padding-bottom: 60px;
            border-bottom: 1px solid #142D6F1A;
            margin-bottom: 60px;
        }
        @include max (767px){
            padding-bottom: 40px;
            border-bottom: 1px solid #142D6F1A;
            margin-bottom: 40px;
        }
    }
    .text4{
        p{
            margin: 0;
            padding-top: 24px;
        }
        ul{
            li{
                font-weight: 500;
            }
        }
    }
    &.feature-wrap{
        position: sticky;
        top: 110px;
    }
}
.pb-60{
    padding-bottom: 60px!important;
