.lonyo-t{
    &-wraper{
        
        position: relative;
        &::before{
            content: "";
            position: absolute;
            top: -100px;
            left: -220px;
            width: 150px;
            height: 150px;
            border-radius: 100px;
            transform: rotate(180deg);
            transform: translatey(0);
            -webkit-animation: updown 3s ease-in-out infinite;
            animation: updown 3s ease-in-out infinite;
            background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
            @include max (1800px){
                display: none;
            }
        }
    }
    &-wrapper2{
        width: 100%;
        height: 700px;
        overflow: hidden;
        position: relative;
        box-sizing: border-box;
        @include max(991px) {
            margin-bottom: 24px;
            height: auto;
        }
    }
    &-wrapper3{
        width: 100%;
        height: 700px;
        overflow: hidden;
        position: relative;
        box-sizing: border-box;
    }
    &-wrap{
        padding: 30px;
        border-radius: 10px;
        margin-bottom: 24px;
        animation: marquee 15s linear infinite;
        &.wrap2{
            white-space: wrap;
        }
    }
    &-ratting{
        padding-bottom: 24px;
    }
    &-text{
        p{
            margin-bottom: 24px;
        }
    }
    &-overlay{
        position: absolute;
        top: 160px;
        left: 0;
        z-index: 1;
        @include max (1800px){
            display: none;
        }
    }
    &-overlay2{
        position: absolute;
        bottom: 0;
        left: 0;
        @include max (1800px){
            display: none;
        }
    }
    &-author{
        display: flex;
        align-items: center;
        gap: 15px;
        &-data{
            p{
                font-weight: 500;
                margin-bottom: 5px;
            }
            span{
                font-size: 18px;
                line-height: 25px;
            }
        }
    }
    // add three
    &-slider-icon{
        white-space: nowrap;
        margin: 0 10px;
    }
}

.lonyo-testimonial-slider-init .slick-slide {
    margin: 0 10px;
}

.lonyo-slider-icon-data2.slick-initialized.slick-slider{
    direction: rtl;
}




/* Make it move! */
@keyframes marqueetop {
    100% { top:   0 }
    0% { top: -700px }
}

@keyframes marqueebottom {
    0%   { top:   0 }
    100% { top: -700px }
}

.marqueeBottom {
    position: relative;
    animation: marqueebottom 10s linear infinite;
    overflow: hidden;
    @include max(991px) {
        animation: inherit!important;
    }
}
.marquree-top {
    position: relative;
    animation: marqueetop 10s linear infinite;
    @include max(991px) {
        animation: inherit!important;
    }
}


